.products__natural {
    margin-bottom: 15px;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
  
    color: #ffa5ba;
  
    @media screen and (min-width: 768px) {
      margin-bottom: 18px;
      font-size: 26px;
      line-height: 30px / 26px;
    }
    @media screen and (min-width: 1199px) {
      margin-bottom: 16px;
      font-size: 30px;
      line-height: 34px / 30px;
    }
    color: #FFA5BA;
  }
  
  .products {
    margin-bottom: 8px;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  
    color: var(--accent-cl-txt);
  
    @media screen and (min-width: 768px) {
      margin-bottom: 20px;
      font-size: 48px;
      line-height: 55px / 48px;
    }
    @media screen and (min-width: 1199px) {
      margin-bottom: 20px;
      font-size: 58px;
      line-height: 66px / 58px;
    }
  }
  .product__carts {
    display: flex;
    flex-wrap: wrap;
    //   flex-direction: column;
    align-items: center;
    justify-content: center;
  
    @media screen and (min-width: 768px) {
      gap: 16px;
    }
    @media screen and (min-width: 1199px) {
      gap: 32px;
    }
  }
  .list {
    position: relative;
    overflow: hidden;
  }
  .product__overlay {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 24px;
    padding-top: 24px;
    background-color: #ce4fee;
  
    transform: translatey(-110%);
    transition: transform 250ms ease-in-out;
  }
  .list:hover .product__overlay {
    transform: translatey(-10%);
  }
  .products__cover {
    position: relative;
    //   overflow: hidden;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 140px;
    width: 280px;
    height: 489px;
    //   background: #ffb8ca;
    border-radius: 24px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    &--pinc {
      background-color: #ffb8ca;
      transition-property: transform;
      transition-duration: 500ms;
      transition-timing-function: ease-in-out;
      transition-delay: 0;
      &:hover {
        transform: scale(1.1);
      }
    }
    &--broun {
      background-color: #f0d1a5;
      transition-property: transform;
      transition-duration: 500ms;
      transition-timing-function: ease-in-out;
      transition-delay: 0;
      &:hover {
        transform: scale(1.1);
      }
    }
    &--broun {
      background-color: #f0d1a5;
    }
    &--green {
      background-color: var(--light-green);
      transition-property: transform;
      transition-duration: 500ms;
      transition-timing-function: ease-in-out;
      transition-delay: 0;
      &:hover {
        transform: scale(1.1);
      }
    }
  
    @media screen and (min-width: 768px) {
      margin-top: 138px;
      width: 224px;
      height: 473px;
    }
    @media screen and (min-width: 1199px) {
      margin-top: 261px;
      width: 368px;
      height: 535px;
    }
  }
  
  .products__cover--pinc::before {
    content: '';
    width: 280px;
    height: 280px;
    margin-top: -140px;
    margin-bottom: 20px;
    background-size: contain;
    @media screen and (min-width: 768px) {
      margin-top: -138px;
      margin-bottom: 32px;
      width: 224px;
      height: 224px;
    }
    @media screen and (min-width: 1199px) {
      margin-top: -261px;
      margin-bottom: 30px;
      width: 368px;
      height: 394px;
    }
  }
  .products__cover--pinc::before {
    //   margin-top: -140px;
    //   margin-bottom: 20px;
    background-image: url('../images/isecream-products-img/prod-img1_S.png');
    background-size: 280px 280px;
    background-repeat: no-repeat;
  
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
