  .products__tipe {
    margin-bottom: 70px;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  
    color: var(--white-txt);
  
    @media screen and (min-width: 1199px) {
      font-size: 30px;
      line-height: 1.13;
    }
    &:before {
      content: '';
      position: absolute;
      top: 44%;
      left: 50%;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: rgb(255, 255, 255);
      box-shadow: -10px 0 0 rgb(255, 255, 255), 10px 0 0 rgb(255, 255, 255);
  
      @media screen and (min-width: 768px) {
        top: 37%;
      }
      @media screen and (min-width: 1199px) {
        top: 43%;
      }
    }
  }
  .products__descr {
    padding-left: 40px;
    padding-right: 40px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    letter-spacing: 0.04em;
  
    color: var(--white-txt);
  
    margin-bottom: 32px;
  
    @media screen and (min-width: 768px) {
      padding-left: 32px;
      padding-right: 32px;
      font-size: 14px;
      line-height: 1.7;
    }
    @media screen and (min-width: 1199px) {
      margin-bottom: 60px;
      padding-left: 62px;
      padding-right: 62px;
      font-size: 16px;
      line-height: 1.7;
    }
  }
  
  .products__button {
    cursor: pointer;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    border: none;
    //   margin-bottom: 40px;
    margin-left: auto;
    margin-right: auto;
    color: var(--accent-cl-txt);
    background-color: var(--white-txt);
    transition-property: box-shadow;
      transition-duration: 350ms;
      transition-timing-function: ease-in-out;
      transition-delay: 0;
  }
  .products__button:hover,
  .products__button:focus {
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  }
  .products__icon {
    margin-left: auto;
    margin-right: auto;
    // position: absolute;
    // z-index: 2;
    fill: var(--accent-cl-txt);
  }
  
